<template>
  <div id="page--products" class="page-container">
    <h1>Product Page</h1>
    <div class="product-wrapper">
      <div class="product__left">
        <div class="product__list">
          <h3>Products</h3>
          <ul>
            <li v-for="item in Products" :key="item.id">
              <a href="#" @click.prevent="selectProd(item)">{{ item.name }}</a>
            </li>
          </ul>
        </div>
      </div>
      <div class="product__right">
        <div class="product__content no-data" v-if="Products.length && !Object.keys(selectedProd).length">
          Please select a product
        </div>
        <div class="product__content no-data no-products" v-else-if="!Products.length">No products</div>
        <div class="product__content has-data" v-else>
          <h3>{{selectedProd.name}}</h3>
          <p v-html="selectedProd.description"></p>
          <hr>
          <strong>{{ selectedProd.available ? "Available" : "Out of Stock" }}</strong>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import './Products.scss'
export default {
  data() {
    return {
      selectedProd: {},
    }
  },
  methods: {
    selectProd(prod) {
      this.selectedProd = prod;
    }
  },
  computed: {
    Products() {
      return this.$store.state.Products;
    },
  },
}
</script>