<template>
  <transition name="modal-fade">
    <div class="modal-backdrop">
      <div
        class="modal"
        role="dialog"
        aria-labelledby="modalTitle"
        aria-describedby="modalDescription"
      >
        <header class="modal-header" id="modalTitle">
          <slot name="header"> {{currentProd.name}} </slot>
          <button
            type="button"
            class="btn-close"
            @click="close"
            aria-label="Close modal"
          >
            x
          </button>
        </header>

        <section class="modal-body" id="modalDescription">
          <add-new-form :prod="currentProd" @submit="formSubmit"></add-new-form>
        </section>
      </div>
    </div>
  </transition>
</template>

<script>
import AddNewForm from "../AddNewForm/AddNewForm";
import "./Modal.scss";
export default {
  name: "Modal",
  components: {
    AddNewForm,
  },
  props: {
    currentProd: { type: Object },
  },
  methods: {
    close() {
      this.$emit("close");
    },
    formSubmit(prod) {
      this.$store.commit('updateProductList', prod);
      this.close();
    }
  },
};
</script>

