import { createStore } from 'vuex';
import {mockData} from './mock-data'

export const store = createStore({
  state: {
    Products: mockData.products,
  },
  mutations: {
    updateProductList(state, product) {
      const cloneProducts = JSON.parse(JSON.stringify(state.Products));
      const findProduct = cloneProducts.find(prod => prod.id === product.id);
      // Edit
      if (findProduct) {
        state.Products = cloneProducts.map(prod => {
          if (prod.id === product.id) {
            prod = product
          }
          return prod;
        })
      } else {
        state.Products.push(product);
      }
    },
    deleteProducts(state, idList) {
      const cloneProducts = JSON.parse(JSON.stringify(state.Products));
      state.Products = cloneProducts.filter(prod => idList.indexOf(prod.id) === -1)
    },
  },
  actions: {},
  modules: {},
});
