<template>
    <form class="add-new-form">
        <label for="name">Name:</label>
        <input id="name" v-model.lazy.trim="name" />
        <label for="review">Description:</label>
        <textarea id="review" v-model.lazy.trim="description"></textarea>
        <label for="rating">Available:</label>
        <select id="rating" v-model.lazy.trim="available">
            <option value="true">Yes</option>
            <option value="false">No</option>
        </select>
        <input class="button" type="submit" value="Submit" @click="onSubmit" />
    </form>
</template>

<script>
import uniqid from "uniqid";
import './AddNewForm.scss'
export default {
    data(){ 
        return {
            name: this.prod === undefined ? '' : this.prod.name,
            description: this.prod === undefined ? '': this.prod.description,
            available: this.prod === undefined ? true : this.prod.available
        }
    },
    props: {
        prod: { type: Object },
    },
    methods: {
        onSubmit(e) {
            e.preventDefault();
            const name = this.name;
            const description = this.description;
            const available = JSON.parse(this.available);
            if (!name.length || !description.length) {
                alert('Product is incomplete. Please fill out every field.')
                return false;
            }
            
            const prodObj = {
                id: this.prod === undefined ? uniqid("product-") : this.prod.id,
                name,
                description,
                available
            }
            this.$emit('submit', prodObj)
            
            this.name = '';
            this.description = '';
            this.available = true
        }
    },
}
</script>