<template>
  <div id="page--admin" class="page-container">
    <h1>Admin Page</h1>
    <span class="form-toggler" v-if="!showAddNewForm" @click="toggleAddNewForm">+ Show add new form</span>
    <span class="form-toggler" v-else @click="toggleAddNewForm">- Hide add new form</span>
    <add-new-form v-if="showAddNewForm" @submit="formSubmit"></add-new-form>
    <admin-data-table></admin-data-table>
  </div>
</template>

<script>
import AddNewForm from "../../components/AddNewForm/AddNewForm";
import AdminDataTable from "../../components/AdminDataTable/AdminDataTable";
import './Admin.scss';

export default {
  components: {
    AddNewForm,
    AdminDataTable
  },
  data() {
    return {
      showAddNewForm: false,
    }
  },
  methods: {
    toggleAddNewForm() {
      this.showAddNewForm = !this.showAddNewForm;
    },
    formSubmit(prod) {
      this.$store.commit('updateProductList', prod)
    }
  },
};
</script>