<template>
  <div class="table-wrapper">
    <table>
      <thead>
        <tr>
          <th>
            <input
              id="checkbox--all"
              type="checkbox"
              :checked="selectedProducts.length === Products.length && Products.length"
              @change="selectAllProds"
            />
          </th>
          <th>No</th>
          <th>ID</th>
          <th>Name</th>
          <th>Description</th>
          <th>Available</th>
          <th></th>
        </tr>
      </thead>
      <tbody v-if="Products.length">
        <tr v-for="(item, index) in Products" :key="item.id">
          <td>
            <input
              class="checkbox"
              type="checkbox"
              @change="selectProduct(item.id)"
              :checked="selectedProducts.indexOf(item.id) > -1"
            />
          </td>
          <td>{{ index + 1 }}</td>
          <td>{{ item.id }}</td>
          <td style="text-align: left; padding: 0 10px">{{ item.name }}</td>
          <td
            v-html="item.description"
            style="text-align: left; padding: 0 10px"
          ></td>
          <td>{{ item.available ? "Available" : "Out of Stock" }}</td>
          <td><input type="button" value="Edit" style="margin: 10px" @click="showModal(item)"/></td>
        </tr>
      </tbody>
    </table>
    <button class="btn-delete" :disabled="!selectedProducts.length || !Products.length" @click="deleteSelectedProds">Delete selected products</button>
    <Modal
      :currentProd="currentProd"
      v-if="Object.keys(currentProd).length"
      v-show="isModalVisible"
      @close="closeModal"
    />
  </div>
</template>

<script>
import Modal from '../../components/Modal/Modal.vue';
import "./AdminDataTable.scss";
export default {
  components: {
    Modal
  },
  data() {
    return {
      currentProd: {},
      selectedProducts: [],
      isModalVisible: false,
    };
  },
  methods: {
    selectAllProds() {
      const _selectedProducts = JSON.parse(
        JSON.stringify(this.selectedProducts)
      );
      if (_selectedProducts.length) {
        this.selectedProducts.length = 0;
      } else {
        this.selectedProducts = this.Products.map((prod) => {
          return prod.id;
        });
      }
    },
    selectProduct(id) {
      const _selectedProducts = JSON.parse(
        JSON.stringify(this.selectedProducts)
      );
      if (_selectedProducts.indexOf(id) > -1) {
        this.selectedProducts = _selectedProducts.filter((prodId) => prodId !== id);
      } else {
        this.selectedProducts.push(id);
      }
    },
    deleteSelectedProds() {
      this.$store.commit('deleteProducts', this.selectedProducts)
      this.selectedProducts.length = 0;
    },
    showModal(prod) {
      this.currentProd = prod;
      this.isModalVisible = true;
    },
    closeModal() {
      this.currentProd = {};
      this.isModalVisible = false;
    }
  },
  computed: {
    Products() {
      return this.$store.state.Products;
    },
  },
};
</script>